.navbar {
    background-color: gray; /* 배경 색상 */
    padding: 20px; /* 여백 */
}

.navbar__list {
    list-style-type: none; /* 기본 리스트 스타일 제거 */
    padding: 0; /* 기본 패딩 제거 */
    display: flex; /* 플렉스 박스 사용 */
    justify-content: center; /* 중앙 정렬 */
}

.navbar__item {
    margin: 0 15px; /* 리스트 항목 간 여백 */
}

.navbar__link {
    text-decoration: none; /* 링크의 기본 밑줄 제거 */
    color: rgb(0, 0, 0); /* 기본 텍스트 색상 */
    padding: 10px 15px; /* 링크에 여백 추가 */
    border-radius: 7px; /* 링크 모서리 둥글게 */
    transition: background-color 0.3s; /* 배경 색상 변화 애니메이션 */
    font-weight: bold; /* 글씨 두껍게 */
}

.navbar__link:hover {
    background-color: lightgray; /* 호버 시 배경 색상 변화 */
}